import React from "react";
import Fb_icon_color from "../Assets/Icons/facebook_icon.png";
import Fb_icon_white from "../Assets/Icons/fb_white.png";
import Instagram_icon_white from "../Assets/Icons/insta_white.png";
import Twitter_icon_color from "../Assets/Icons/twitter_icon.png";
import Instagram_icon_color from "../Assets/Icons/instagram_icon.png";
import CentraleMarseille_icon_color from "../Assets/Icons/CM_color.png";
import CentraleMarseille_icon_white from "../Assets/Icons/CM_white.png";
import LinksSocialNetworks from "../content/LinksSocialNetworks";
import AlphaIMG from "../Assets/SocialNetworks/Fb_Alpha.png";
import EcoleIMG from "../Assets/SocialNetworks/Fb_ECM.png";
import LeftArrow from "../Assets/Buttons/Buttons__scroll_sp_left_black.png"
import "./styles/SocialNetworkLink.css";
import onClickOutside from "react-onclickoutside";



class SocialNetworksLink extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            clicked:false,
        }
        this.AlphaAndEcole = (this.props.SocialNetwork === "Facebook" || this.props.SocialNetwork === "Instagram")
        this.Classname="SocialNetworkIcon_"+this.props.Color
        this.id=this.props.SocialNetwork+"_"+this.props.Color
        this.Icons = {
            Facebook_white: Fb_icon_white,
            Instagram_white: Instagram_icon_white,
            Facebook_color: Fb_icon_color,
            Instagram_color: Instagram_icon_color,
            Twitter_color: Twitter_icon_color,
            CentraleMarseille_color: CentraleMarseille_icon_color,
            CentraleMarseille_white: CentraleMarseille_icon_white,
        }
    }
    openAlphaEcoleChoice = () => {
        if (!this.state.clicked){
            let LinksDiv = document.getElementById("LinksContainer_"+this.props.Number);
            LinksDiv.style.display = "table-cell";
            this.setState({
                clicked:true,
            })
        }
        else{
            this.closeAlphaEcoleChoice();
        }
    }

    closeAlphaEcoleChoice = () => {
        let LinksDiv1 = document.getElementById("LinksContainer_"+"1");
        LinksDiv1.style.display = "none";
        let LinksDiv2 = document.getElementById("LinksContainer_"+"2");
        LinksDiv2.style.display = "none";
        this.setState({
            clicked:false,
        })
    }


    render() {
        if (this.AlphaAndEcole){
            SocialNetworksLink.handleClickOutside = () => this.closeAlphaEcoleChoice()
            return(
                <div className={"SocialNetworksLink-container1"}>
                    <img className={this.Classname} id={this.id} src={this.Icons[this.props.SocialNetwork+'_'+this.props.Color]} onClick={this.openAlphaEcoleChoice}/>
                    <div id={"LinksContainer_"+this.props.Number} className={"LinksContainer"}>
                        <a href={LinksSocialNetworks[this.props.SocialNetwork]["Alpha"]} target="_blank" className="SocialNetworkButton LinksContainerItems"> <img className="SocialNetworkIcon" src={AlphaIMG}/></a>
                        <a href={LinksSocialNetworks[this.props.SocialNetwork]["Ecole"]} target="_blank" className="SocialNetworkButton LinksContainerItems"> <img className="SocialNetworkIcon" src={EcoleIMG}/></a>
                        <img className="closeArrowSocialNetworks LinksContainerItems" src={LeftArrow} onClick={this.closeAlphaEcoleChoice}/>
                    </div>
                </div>
            )

        }
        else{
            return(
                <div className={"SocialNetworksLink-container2"}>
                    <a href={LinksSocialNetworks[this.props.SocialNetwork]} target="_blank" className="SocialNetworkButton">
                        <img className={this.Classname} id={this.id} src={this.Icons[this.props.SocialNetwork+'_'+this.props.Color]}/></a>
                </div>
            )
        }
    }
}
const clickOutsideConfig = {
    handleClickOutside: () => SocialNetworksLink.handleClickOutside
};

export default onClickOutside(SocialNetworksLink, clickOutsideConfig);
