import React from 'react';
import ButtonImage from '../Assets/Buttons/Buttons__menu.png';
import Logo_sans_texte from '../Assets/Logos_site/Logos__site_sanstexte.png';
import Menu from "./Menu";
import Backdrop from "./BackDrop";
import SlideDrawer from "./SlideDrawer";
import {NavLink} from "react-router-dom";
import Mouette from "../Assets/Menu/Mouette.png";
import Mouette_droite from "../Assets/Menu/Mouette_droite.png";


export default class NavBar extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            windowWidth:window.innerWidth,
            buttonCloseState:"none"
        }
    }

    handleResize = () => {
        this.setState({
            windowWidth: window.innerWidth
        });
    };

    drawerToggleClickHandler = () => {
        this.setState({
            drawerOpen: !this.state.drawerOpen,
            buttonCloseState:"content"
        })
    };

    backdropClickHandler = () => {
        this.setState({
            drawerOpen: false,
            buttonCloseState:"none"
        })
    };
    render() {
        if (this.state.windowWidth > 768){
            const style_logo_sanstexte = {
                height: "40px",
                position:"absolute",
                top:"50%",
                transform:"translateY(-50%)",
                left:"1vw"
        };
            return(
                <div className="NavBar">
                    <NavLink to={"/"}>
                        <img src={Logo_sans_texte} style={style_logo_sanstexte} className="MenuLogo"/>
                    </NavLink>
                    <img src={Mouette} width="50px" className="Mouette"/>
                    <Menu/>
                </div>
            )
        }
        else{
            return(
                <div>
                <div className="NavBar">
                    <NavLink to={"/"}>
                        <img src={Mouette_droite} height="10px" className="MouetteNavV"/>
                    </NavLink>
                    <img src={ButtonImage} width="50px" onClick={this.drawerToggleClickHandler} className="MenuButtonOpen"/>
                </div>
                    <Backdrop show={this.state.drawerOpen} close={this.backdropClickHandler}/>
                    <SlideDrawer show={this.state.drawerOpen} toggle={this.drawerToggleClickHandler}/>
                </div>
            )
        }
    }

    componentDidMount() {
        window.addEventListener("resize",this.handleResize);
    }

}
