import React from 'react';
import './styles/AlphaButtons.css';
import {NavLink, withRouter} from "react-router-dom";
import { AnimationWrapper } from 'react-hover-animation'
import Mouette from "../Assets/Menu/Mouette.png";

class NavItem extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        function displayCircle() {
            isVisited = 'block';
        }
        let classItem = 'MenuItem';
        let vertical = '';
        let position = '';
        let isVisited="none";
        if(this.props.vertical){
            vertical = 'V ';
            classItem = classItem + vertical
        }
        if(this.props.visited){
            classItem = classItem + ' visited' + vertical + position;
            isVisited="block"
        }
        if (this.props.vertical){
           return (
               <div className={classItem}>
                   <NavLink to={this.props.to}>
                       <a onClick={() => {this.props.toggle(); this.props.onClick(this.props.number)}}>
                           {this.props.title}
                       </a>
                       <img src={Mouette} width="20px" style={{display:isVisited}} className="MouetteV"/>
                   </NavLink>
               </div>
           )
        }
        else{
            return (
                <div className={classItem} >
                    <NavLink to={this.props.to}>
                        <AnimationWrapper className="titre_menu">
                            <a onClick={()=>{this.props.onClick(this.props.number);}}>
                                {this.props.title}
                            </a>
                            <p className="circle" style={{display:isVisited} }></p>
                        </AnimationWrapper>
                    </NavLink>
                </div>
            )

        }
    }
}

export default withRouter(NavItem);
