import React from "react";
import * as PIXI from 'pixi.js';
import wave from "../Assets/Footer/wave.svg"
import './styles/Wave.css';
import grounge from "../Assets/Footer/grounge.png"


export default class Wave extends React.Component{
    constructor(props) {
        super(props);
        this.state={}
        this.valueForWave= 10;
        if (window.innerWidth > 768){
            this.valueForWave=20
        }
        this.width=window.innerWidth;
        this.count=0;
        this.app = new PIXI.Application({ width:window.innerWidth, height: 130, backgroundAlpha:0})
        this.ropeLength = window.innerWidth/ this.valueForWave;
        this.points=[];
        for (let i = 0; i < 40; i++)
        {
            this.points.push(new PIXI.Point(i * this.ropeLength, 0));
        }
        this.strip= new PIXI.SimpleRope(PIXI.Texture.from(wave), this.points);
        this.strip.x = 0;
        this.snakeContainer = new PIXI.Container();
        this.snakeContainer.position.x = -50;
        this.snakeContainer.position.y = 100;
        this.snakeContainer.scale.set(1);
        this.app.stage.addChild(this.snakeContainer);
        this.snakeContainer.addChild(this.strip)
        this.app.ticker.add(() => {
            this.count -= 0.005;
            for (let i = 0; i < this.points.length; i++) {
                this.points[i].y = Math.sin((i * 0.5) + this.count) * 15;
                this.points[i].x = i * this.ropeLength + Math.cos((i * 0.3) + this.count) * 25;
            }
        })
    }
    componentDidMount() {
        const element = document.getElementById('myPixiDiv');
        element.appendChild(this.app.view);
    }
    render() {
        console.log(this.valueForWave,'hkfeg')
        return (
            <div id='myPixiDiv' style={{width:"100vw"}}>
                <img id="grounge" src={grounge} alt={"grounge"}/>
            </div>
        )
    }
}