import React from 'react';
import { NavLink, withRouter } from 'react-router-dom'
import NavItem from "./NavItem";
import ButtonClose from "../Assets/Buttons/Buttons__croix.png";
import logo_sans_text from "../Assets/Logos_site/Logos__site_sanstexte.png";

class Menu extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            visited : [1,0,0,0,0,0]
        }
    }

    pages = [
        '/',
        '/marseille',
        '/associations',
        '/reseaux',
        '/calendrier',
        '/contact'
    ]

    handleMenu = (number) => {
        let tableau = [];
        tableau[number]=1;
        this.setState({visited : tableau});
        console.log(this.state.visited)
    };

    render(){
        const style_item = {
            display: "grid",
            margin: "auto"
        };
        let className = 'Menu';
        let navMenuOpenVertical
        if(this.props.vertical){
            className = 'MenuV';
            navMenuOpenVertical=<nav className="navMenuOpenVertical">
                <img src={logo_sans_text} onClick={() => {this.props.toggle()}} className="LogoMenuButtonClose"/>
                <img src={ButtonClose} onClick={() => {this.props.toggle()}} className="MenuButtonClose"/>
            </nav>
        }
        return(
            <div>
                {navMenuOpenVertical}
                <div className={className}>
                    <NavItem toggle={this.props.toggle} title={'Accueil'} onClick={this.handleMenu} to={'/'} number={0} vertical={this.props.vertical} visited={this.state.visited[0]}/>
                    <NavItem toggle={this.props.toggle} title={'Marseille'} onClick={this.handleMenu} to={'/marseille'} number={1} vertical={this.props.vertical} visited={this.state.visited[1]} />
                    <NavItem toggle={this.props.toggle} title={'Associations'} onClick={this.handleMenu} to={'/associations'} number={2} vertical={this.props.vertical} visited={this.state.visited[2]}/>
                    <NavItem toggle={this.props.toggle} title={'Réseaux'} onClick={this.handleMenu} to={'/reseaux'} number={3} vertical={this.props.vertical} visited={this.state.visited[3]} />
                    <NavItem toggle={this.props.toggle} title={'Calendrier'} onClick={this.handleMenu} to={'/calendrier'} number={4} vertical={this.props.vertical} visited={this.state.visited[4]}/>
                    <NavItem toggle={this.props.toggle} title={'Contact'} onClick={this.handleMenu} to={'/contact'} number={5} vertical={this.props.vertical} visited={this.state.visited[5]}/>
                </div>
            </div>
        )
    }

    componentDidMount() {
        this.handleMenu(this.pages.indexOf(this.props.location.pathname));
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.location.pathname !== this.props.location.pathname){
            this.handleMenu(this.pages.indexOf(this.props.location.pathname))
        }
    }
}

export default withRouter(Menu);
