import "./styles/FooterV2.css";
import React, {lazy} from 'react';
import Wave from "../Components/Wave"
import Form from "./Form";
import SocialNetworksLink from "./SocialNetworksLink";

export default class FooterV2 extends React.Component {
    constructor(props){
        super(props)
    }
    render() {
        let vertical = '';
        if(this.props.vertical){
            vertical = 'V ';
        }
        return(
            <div id="Footer">
                <div id="wave">
                    <Wave/>
                </div>
                <div id="logo">
                    <SocialNetworksLink Number={1}  SocialNetwork={"Facebook"} Color={"white"} />
                    <SocialNetworksLink Number={2} SocialNetwork={"Instagram"} Color={"white"}/>
                    <SocialNetworksLink SocialNetwork={"CentraleMarseille"} Color={"white"}/>
                    <a id="BTNVisiteVirtuelle" href="https://julie8398.github.io/PTvisitevirtuelle/" target="_blank"> Visite Virtuelle de Centrale Marseille </a>
                </div>
            </div>
        )
    }
}